/*
body {  // so that preventDefaults work for the full window
  margin: 0;
  height: 100%;
}
*/

.drop-area {
  position: relative;  // required to put the close button on the top right
  //border: 1px dashed #ccc;
  &.hilight {
    outline: 1px dashed Magenta;
    box-shadow: 0 0 12px 4px rgba(0,255,255,.6);
  }
}

.drop-gallery {
  // to place on the overlay
  //position: absolute;
  //left: 0;
  //top: 0;
  // ***********************
  display: flex;
  flex-wrap: wrap;
  margin: 10px auto;
  div {  // <video> wrapper
    position: relative;
    margin: 4px;
    &::before, &::after {  // common
      position: absolute;
      font-size: xx-small;
      text-shadow: 1px 1px 2px Black;
      z-index: 100;
    }
    &::before {
      margin-left: 2px;
      content: attr(data-title);
      color: Cyan;
    }
    &::after {
      //transform: translateX(-55px);
      margin-top: 10px;
      margin-left: -50px;
      text-align: right;
      content: attr(data-duration) ' ' attr(data-width) 'x' attr(data-height);
      color: White;
    }
  }
  img, video {  // common
    //*************** to fill a div keeping aspect ratio
    object-fit: cover;
    overflow: hidden;
    //***************
    vertical-align: top;
    border-radius: 6px;
    box-shadow: 2px 2px 4px 2px rgba(10,10,10,.6);
    &:hover {
      transition: box-shadow .3s ease-out;
      //transform: scale(2);
      box-shadow: 0 0 12px 4px rgba(255,0,255,.6);
    }
  }
  img {
    width: 120px;
    height: 100%;
    margin: 4px;
  }
  video {
    width: 180px;
  }
}

.drop-list-btn {
  padding-left  : 100px;
  padding-right : 100px;
  //padding-top   : 4px;
  //padding-bottom: 4px;
  margin-top : 4px;
  margin-left: 4px;
  box-shadow: 1px 1px 2px 1px Gray;
  border-radius: 4px;
}

.drop-list {
  list-style-type: decimal;
  font-size: x-small;
  padding-left: 20px;
  & span {
    font-size: xx-small;
   &:first-child {  // filetype
      color: DimGray;
    }
   &:nth-child(3) {  // file size
      color: Purple;
    }
  }
}

// https://developer.mozilla.org/en-US/docs/Web/API/File/Using_files_from_web_applications#Example_Using_object_URLs_to_display_images
.da-form {  // drop-area form
  margin-bottom: 10px;
}

.df-label {  // drop-from label (button)
  display: inline-block;
  padding: 10px;
  background: #ccc;
  cursor: pointer;
  border-radius: 5px;
  border: 1px solid #ccc;
  &:hover {
    background: #ddd;
  }
}

.btn-clear {  // btn clear
  position: absolute;
  top: 0;
  right: 0;
  margin-left: 10px;
  padding: 3px;
  border: 1px solid rgba(10,10,10,.2);
  font-size: xx-small;
  border-radius: 4px;
}

input.vis-hid { // visually-hidden technique (to be keyboard-accessible)
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  clip: rect(1px, 1px, 1px, 1px);
  &:focus + label,
  &:focus-within + label {
    //outline: thin dotted;
    outline: 2px dotted #e73;
  }
}

.drop-help {  // Show help text only when no dropped files exist
//  &[data-count='0']::before {
  width: 100%;
  text-align: center;
  font-family: Cursive;
  font-size: large;
  color: Navy;
  text-shadow: 0 0 10px Yellow, -1px -1px 1px Orange, 1px 1px 1px Green;
}
