.geoTop {
  //border: 1px solid CornSilk;
  transition: transform 1.5s ease-out;
}

.map_border {
//  fill: #aadaff;
  fill: Navy;
  fill-opacity: 0;
  stroke: Blue;
  stroke-width: .5;
  opacity: .5;
}

.graticule {
  fill: none;
  stroke: Blue;
  stroke-width: .5;
  opacity: .5;
  filter: url(#glow);  // <svg> filter
}

.tropics {
  fill: none;
  stroke: red;
  opacity: .5;
  stroke-width: .2;
  filter: url(#glow);
}

.tropic0 {
  fill: none;
  stroke: red;
  opacity: .5;
  stroke-width: .5;
  filter: url(#glow);
}

.land {
  fill: Tan;
  fill-opacity: .05;
  stroke: Cyan;
  stroke-width: .6;
  filter: url(#glow);
  path:hover {
    fill: pink;
    fill-opacity: .2;
    stroke: red;
    stroke-width: .1;
  }
  transition: 1s;
  //animation: blink 2s linear infinite alternate;
}

@keyframes blink {
  to { opacity: .1; }
}

.countries {
  fill: #ffedcd; // fallback color, each countries' color will be assigned in json
  fill-opacity: .2;
  stroke: Navy;
  stroke-width: .5;  // override
  stroke-opacity: .5;
  filter: url(#glow);
  path:hover {
    fill: pink;
    fill-opacity: .5;
    stroke: red;
    stroke-width: .1;
  }
}

.glow_ball1 {
  position: absolute;
  left: 210px;
  top: 282px;
  width: 400px;
  height: 400px;
  border-radius: 50%;
  box-shadow:
    inset     0 0  50px #fff, /* inner white */
    inset  20px 0  80px #f0f, /* inner left magenta short */
    inset -20px 0  80px #0ff, /* inner right cyan short */
    inset  20px 0 400px #f0f, /* inner left magenta broad */
    inset -20px 0 400px #0ff, /* inner right cyan broad */
              0 0  50px #fff, /* outer white */
          -10px 0  80px #f0f, /* outer left magenta */
           10px 0  80px #0ff; /* outer right cyan */
}

.glow_ball0 {
  position: absolute;
  left: 210px;
  top: 282px;
  width: 400px;
  height: 400px;
  background-color: #aadaff; //#3b4ba3;
  border-radius: 50%;
  box-shadow: inset -25px -25px 40px rgba(0,0,0,.5);
  background-image: linear-gradient(-45deg, rgba(255,255,220,.2) 0%, transparent 100%);
  z-index: -1;
}

.btnCont {
  position: absolute;
  display: flex;
  left: 0;
  top: 0;
  //flex-direction: column;
  //flex-wrap: wrap;
}

.btnDemo {
  background-color: white;
  border: 2px solid #4caf50;
  color: black;
  padding: 4px 4px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: x-small;
  margin: 4px 2px;
  opacity: 0.05;
  &:hover {
    opacity: 1;
  }
  transition: opacity .5s;
}

.report {
  font: small "Courier New", sans-serif;
  text-align: left;
  text-indent: 10px;
  line-height: 10px;
}
