@mixin btnDemo($borderColor) {
  background-color: Gainsboro;
  border: 2px solid $borderColor;
  color: black;
  padding: 4px 4px;
  font: small "Courier New", sans-serif;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 10px;
}

.btn-cont {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-right: 10px;
  padding-top: 120px;
}

.test-btn {
  left: 10px;
  top:  10px;
  padding: 4px;
  margin:  2px;
  border-radius: 10%;
  @include btnDemo(#4caf50);
}

.test-btn:disabled,
test-btn[disabled] {
  border: 1px solid olive;
  background-color: darkgray;
  color: SlateGray;
}

/*
Video Editor Layers
.ve-cont: VE Container
  <canvas id="#v-ed">
.ve-place
*/
$VE_W: 800px;
$VE_H: 450px;
.ve-cont {
  display: inline-block;
  position: relative;
  width : $VE_W;
  height: $VE_H;
  margin: 0 auto;
  outline: thick ridge DimGray;
  transition: 3s;
}
.ve-l0 {  // video editor layer 0 background
  //background: navy;
  position: absolute;
  left: 0;
  top: 0;
  width : $VE_W;
  height: $VE_H;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);  // clip inside the view
  background-repeat: no-repeat;
  background-size: cover;
  //background-image: url('./demo/CafeHabanaMalibu.jpg');
  background-image: url('./demo/space.jpg');
  z-index: 0;
}
#v-ed {  // video editor <canvas>
  position: relative;
  cursor: pointer;
  &:hover {
    box-shadow: 0 0 10px 8px Cyan;
  }
  //pointer-events: none;
  z-index: 50;
}
.ve-l1 {  // layer
  position: absolute;
  left: 0;
  top: 0;
  width : $VE_W;
  height: $VE_H;
  opacity: 1;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);  // clip inside the view
  //outline: 30px ridge DimGray;
  //transform: scale(.15) translateX(320px) translateY(-680px) rotateX(10deg) rotateY(10deg);  // Fly Me ...
  //transform: translateX(0px);
  //transform: translateX(300px) translateY(-100px) scale(.5);
  z-index: 20;
}
