.ckpalette {
  text-align: right;
  margin-top: -12px;
  margin-right: 12px;
  font-size: small;
  > span {
    outline: 1px solid black;
    cursor: pointer;
  }
}

.back-list {
  img, video {
    box-shadow: 1px 1px 2px 2px gray;
    border-radius: 5px;
    vertical-align: top;
    margin-right : 4px;
    margin-bottom: 4px;
    width: 152px;
    &:hover {
      box-shadow: 0 0 2px 1px Cyan;
    }
  }
}

.footer {
  text-align: center;
  width: 100%;
  font-family: Courier New;
  font-size: small;
}

.text-in {
